.s {
	height: auto;
	min-height: 100vh;
	width: auto;
	background-color: #f6f6f6;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	padding-top: 53px;
}
.sForm {
	background-color: white;
	height: auto;
	min-width: 900px;
	width: 50%;
	max-width: 1226px;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	padding-left: 174px;
	padding-right: 174px;
	padding-top: 67px;
	padding-bottom: 63px;
}

.s1 {
	height: 423px;
	width: 100%;
	border: 2px #4386a9;
	border-style: dashed;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	position: relative;
	overflow: hidden;
}

.s1 p {
	font-style: normal;
	font-weight: 500;
	font-size: 20px;
	line-height: 38px;
	color: #4386a9;
}

.s1 input {
	height: 100%;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	cursor: pointer;
}

.fileBox {
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: 2;
	background-color: #f7f7f7;
	pointer-events: none;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.errorImageBox {
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: 2;
	background-color: #fff1f1;
	pointer-events: none;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.fileBox button,
.saveBtn {
	width: 176px;
	height: 60px;
	border: none;
	color: white;
	background-color: #62a1eb;
	border-radius: 8px;
	cursor: pointer;
}

.s1:hover > .fileBox {
	background-color: #fdfcfc;
}
.s1:hover > .fileBox button {
	background-color: #a2c4eb;
}

.s1:hover {
	border: 2px solid #62a1eb;
}

.s2,
.s4,
.s5 {
	width: 100%;
	display: grid;
	grid-template-columns: 40% 40%;
	align-items: center;
	justify-content: space-between;
}

.forBorder {
	background-color: #c7c7c7;
	width: 100%;
	height: 1px;
	margin-top: 50px;
	margin-bottom: 50px;
}

.s1,
.s2,
.s3,
.s4,
.s5,
.s6 {
	margin-bottom: 61px;
}

.s2 div,
.s3 div,
.s5 div {
	display: flex;
	flex-direction: column;
	width: 100%;
}

.s2 input,
.s3 input,
.s4 input[type="text"],
.s5 input {
	width: 100%;
	height: 60px;
	outline: none;
	border: 1.8px solid #8ac0e2;
	border-radius: 8px;
	padding-left: 16px;
	padding-right: 16x;
	font-style: normal;
	font-weight: 400;
	font-size: 16px;
	line-height: 21px;
	margin-top: 20px;
	margin-bottom: 20px;
}

.s2 select,
.s3 select {
	width: 100%;
	height: 60px;
	background-color: #ebebeb;
	font-style: normal;
	font-weight: 500;
	font-size: 18px;
	line-height: 21px;
	outline: none;
	border: none;
	padding-left: 16px;
	cursor: pointer;
}

.s3 {
	display: grid;
	grid-template-columns: 30% 30% 30%;
	justify-content: space-between;
	align-items: center;
	width: 100%;
}

.s4_radio {
	height: 100%;
	display: flex;
	flex-direction: column;
}

.radioInputDiv {
	display: flex;
	height: 100%;
	display: flex;
	align-items: center;
}

.radioInputDiv div {
	width: 30%;
}

.s6 {
	width: 100%;
	height: 113px;
	display: flex;
	flex-direction: column;
}
.s6_radio {
	display: flex;
	height: 100%;
}
.s6_radio div {
	min-width: 15%;
	height: 100%;
	display: flex;
	align-items: center;
}

.s7 {
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.nonDisplay {
	display: none;
}

.imgDiv {
	display: flex;
	justify-content: center;
	position: relative;
	height: 100%;
	width: 100%;
	background-color: #8ac0e2;
	z-index: 5;
	pointer-events: none;
	align-items: center;
	outline: hidden;
}

.imgDiv p {
	color: #f7f7f7;
	position: absolute;
	z-index: 5;
	bottom: 0;
}
.laptopImage {
	height: 50%;
	width: auto;
	position: absolute;
	z-index: 5;
}

.redberryLogo {
	width: 100%;
	height: 90px;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 67px;
	margin-bottom: 67px;
}

.errorImageDiv {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.errorImageDiv p {
	color: red;
}

.errorImgLogo {
	background: url("../svg/errorimglogo.svg") no-repeat;
	height: auto;
	width: 37px;
	height: 34px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.redberryLogo div {
	background: url("../svg//redberrylogo.svg") no-repeat;
	height: 85px;
	width: 85px;
}

input[type="radio"] {
	cursor: pointer;
	color: #62a1eb;
	border: 1px solid green;
}

.sBackButton {
	font-family: "Helvetica Neue";
	font-style: normal;
	font-weight: 500;
	font-size: 18px;
	line-height: 21px;
	border: none;
	color: #62a1eb;
	cursor: pointer;
}

@media only screen and (max-width: 390px) {
	.redberryLogo {
		display: none;
	}

	.sForm {
		width: 100vw;
		display: flex;
		flex-direction: column;
		padding-bottom: 58px;
	}

	.s1 {
		height: 244px;
		width: 353px;
		border: 2px #4386a9;
		border-style: dashed;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		position: relative;
		overflow: hidden;
	}

	.s1 p {
		font-family: "Helvetica Neue";
		font-style: normal;
		font-weight: 400;
		font-size: 16px;
		line-height: 26px;
	}

	.s1 button,
	.forBorder {
		display: none;
	}

	.mobilephotosvg {
		background: url("../svg/mobilephoto.svg") no-repeat;
		height: 54px;
		width: 54px;
	}

	.s2,
	.s3,
	.s5,
	.s6,
	.s7 {
		width: 358px;
		display: flex;
		flex-direction: column;
	}

	.s2 input,
	.s3 input,
	.s5 input {
		width: 339px;
		height: 60px;
		outline: none;
		border: 1.8px solid #8ac0e2;
		border-radius: 8px;
		padding-left: 16px;
		padding-right: 16x;
		font-style: normal;
		font-weight: 400;
		font-size: 16px;
		line-height: 21px;
		margin-top: 20px;
		margin-bottom: 20px;
	}

	.s4 {
		display: flex;
		flex-direction: column;
		height: 300px;
	}

	.s4_input {
		width: 339px;
		height: 60px;
	}
	.s4_radio {
		height: 104px;
		width: 339px;
	}

	.s5 {
		display: flex;
		flex-direction: column;
	}
	.laptopImage {
		width: 90%;
		height: auto;
	}

	.s7 {
		flex-direction: row;
	}

	.sBackButton {
		border: none;
		background-color: white;
		color: #62a1eb;
		font-family: "Helvetica Neue";
		font-style: normal;
		font-weight: 500;
		font-size: 18px;
		line-height: 21px;
	}

	.priceDiv {
		position: relative;
		display: flex;
		justify-content: center;
	}
	.gelicon {
		background: url("../svg/gelicon.svg") no-repeat;
		width: 13px;
		height: 15.7px;
		position: absolute;
		left: 320px;
	}
}
